import React from 'react';

const ST_DEST = (props) => {
  return(
    <div className="menu menu-item center flex column">
      <h3>Target Account</h3>
        <form onSubmit={props.importFromList}>
          <div className="dest-menu">
            <div className="flex column space-between">
              <label>API Key:</label>
              <input onChange={props.handleChange} name="dest_key" value={props.dest_key} />
            </div>
            <div className="flex column space-between">
              <label>Secret:</label>
              <input onChange={props.handleChange} name="dest_secret" value={props.dest_secret} />
            </div>
          </div>
          <div className="flex column space-around">
            <button id="upload" type="submit" className="submit-btn">UPLOAD</button>
          </div>
        </form>
        <div className="menu flex column space-around">
          <p className="instructions"><i>Configure the account that you'd like import templates to and click "UPLOAD".</i></p>
        </div>
    </div>
  );
};

export default ST_DEST;



