import React from 'react';

const ST = (props) => {
  return(
    <div className="flex column flex-start">
      <form onSubmit={props.getList} className="flex column space-around container-xxl">
        <div className="menu-item">
          <div className="flex column space-between container-xxl">
            <label>API Key:</label>
            <input onChange={props.handleChange} name="src_key" value={props.src_key} />
          </div>
          <div className="flex column space-between container-xxl">
            <label>API Secret:</label>
            <input onChange={props.handleChange} name="src_secret" value={props.src_secret} />
          </div>
        </div>
        <button type="submit" className="submit-btn">GET LIST</button>
      </form>
    </div>
  )
}

export default ST;



