import React from 'react';
import ModalToggle from './ModalToggle';

const ErrorMsg = (props) => {

  return (
    <div id="error-modal-content" className="invisible">
      <ModalToggle hideModal={props.hideModal} />
      <p>There has been an error {props.error.action} {props.error.service}.</p>
      <p>Message: {props.error.errormsg}</p>
      <p style={{fontSize: "0.8em"}}><i>Disallowed IP? If your target account whitelists IP addresses, you'll need to add it there.</i></p>
    </div>
  )
}

export default ErrorMsg;



