import React from 'react';

import ImportMenu from './import';

const ImportContainer = (props) => {
  return(
    <div className="menu flex column flex-start">
      <h3>Import Source</h3>
      <ImportMenu
        handleChange={props.handleChange}
        getList={props.getList}
        src_key={props.src_key}
        src_secret={props.src_secret}
      />
      <div className="menu flex column space-around">
        <p className="instructions"><i>Configure the account that you'd like to retrieve templates from and click "Get List".</i></p>
      </div>
    </div>
  )
}

export default ImportContainer;



