import React from 'react';
import Console from './Console';
import ErrorMsg from './ErrorMsg';

const ModalContainer = (props) => {
  return (
    <div id="modal-container" className="invisible">
      <Console messages={props.messages} hideModal={props.hideModal} />
      <ErrorMsg error={props.error} hideModal={props.hideModal} />
    </div>
  )
}

export default ModalContainer;



