import React from 'react';
import Checkbox from './Checkbox';

const TemplateListItem = (props) => {
  const templateA = props.template.slice(0,40);
  const templateB = props.template.slice(40);

  if (props.index % 2 == 0) {
    return(
      <div className="template-list-item flex row flex-start menu-item">
        <Checkbox selected_templates={props.selected_templates} template={props.template} selectTemplate={props.selectTemplate} />
        <p>{templateA} {templateB}</p>
      </div>
    )
  } else {
    return (
      <div className="template-list-item flex row flex-start menu-item" style={{backgroundColor: '#EEE'}}>
        <Checkbox selected_templates={props.selected_templates} template={props.template} selectTemplate={props.selectTemplate} />
        <p>{templateA} {templateB}</p>
      </div>
    )
  }
}

export default TemplateListItem;



