import React from 'react';

import TemplateListItem from './TemplateListItem';

const TemplateList = (props) => {
  const templateListItems = props.templates.map((template, index) => {
    return <TemplateListItem index={index} template={template} selectTemplate={props.selectTemplate} selected_templates={props.selected_templates} key={1000*Math.random()} />
  })


  return(
    <div className="template-menu flex column menu-item" id="template_list">
      {templateListItems}
    </div>
  )
}

export default TemplateList;



