import React from 'react';

import Message from './Message';
import ModalToggle from './ModalToggle';

const Console = (props) => {
  const messageList = props.messages.map(message => <Message text={message} key={1000*Math.random()} />)

    return(
      <div id="console-modal-content" className="menu flex column flex-start align-start invisible">
      <ModalToggle hideModal={props.hideModal} />
        <h3 className="center-self">Upload Results</h3>
        <div className="flex column flex-start">
          {messageList}
        </div>
      </div>
    )
}

export default Console;



