// Temporarily removed header:
// <h3>2. Select Templates To Upload</h3>

import React from 'react';

import TemplateList from './TemplateList';

const TemplateListContainer = (props) => {
  if (props.templates.length > 0) {
    return(
      <div className="menu flex column flex-start" id="template_list">
      <p><i>Select the templates that you would like to import...</i></p>
        <form id="template-form" onSubmit={props.importFromList}>
          <TemplateList
            templates={props.templates}
            selectTemplate={props.selectTemplate}
            selected_templates={props.selected_templates}
          />
        </form>
        <div className="select-all-container">
          <button className="submit-btn" onClick={props.selectAll}>SELECT ALL</button>
          <button className="submit-btn" onClick={props.deselectAll}>DESELECT ALL</button>
        </div>
        <label>
          <input type="checkbox" name="include_teams" onClick={props.toggleOption} style={{ width: 'auto' }} />
          <b>Include teams with template.</b><i> Team must exist in both target and source account to complete transfer.</i>
        </label>
      </div>
    )
  } else {
    return(
      <div className="inactive flex column space-around">
        <h3>2. Select Templates To Upload</h3>
        <h4>Configure upload source and hit<br /> 'SUBMIT' to view template list.</h4>
      </div>
    )
  }
}

export default TemplateListContainer;



