import React, { Component } from 'react';
import axios from 'axios';

import ImportContainer from '../import-menu/import-container';
import TemplateListContainer from '../template-list/TemplateListContainer';
import ST_Dest from '../import-menu/st_dest';
import Sailthru_Logo from '../../images/Sailthru_Logo.png';
import ModalContainer from '../modal/Modal';

export default class Main extends Component {
  constructor(props) {
    super(props);

    this.state = {
      src_key: '',
      src_secret: '',
      dest_key: '',
      dest_secret: '',
      templates: [],
      selected_templates: [],
      messages:[],
      modal_type: '',
      include_teams: false,
      error: {
        statusCode: '',
        errormsg: '',
        action: '',
        service: '',
        error: ''
      }
    };
  }


  handleChange(event, field) {
    if (field) {
      this.setState({[field]: { ...this.state[field], [event.target.name]: event.target.value } });
    } else {
      this.setState({ [event.target.name] : event.target.value });
    }
  }

  selectTemplate(event) {
    const selected_templates = this.state.selected_templates;

    if(event.target.checked) {
      selected_templates.push(event.target.value);
    } else {
      const index = selected_templates.indexOf(event.target.value);
      selected_templates.splice(index, 1);
    }

    this.setState({selected_templates});
  }

  toggleOption(event) {
    this.setState({ [event.target.name] : event.target.checked });
    console.log(event.target.checked);
  }

  selectAll() {
    const allTemplates = this.state.templates;
    this.setState({selected_templates: allTemplates});
  }

  deselectAll() {
    this.setState({selected_templates: []});
  }


  getList(event) {
    event.preventDefault();

    axios.get(`/templates`, {
      params: {
        apiKey: this.state.src_key,
        secret: this.state.src_secret
      }
    })
    .then(res => {
      if (res.data.length == 0) {
        this.setState({error:
          {
            action: 'importing templates to Sailthru.',
            errormsg: 'No templates found. It is possible that you may not have the correct path.',
            source: ''
          }
        });
        this.revealModal('error');
      } else if (!Array.isArray(res.data) && res.data.hasOwnProperty('errormsg')) {
        this.setState({error: res.data});
        this.revealModal('error');
      } else {
        this.setState({templates: res.data});
      }
    })
    .catch(err => console.log(err));
  }

  importFromList(event) {
    this.setState({messages: []});
    event.preventDefault();

    if (this.state.selected_templates.length < 1) {
      this.setState({error:
        {
          action: 'importing templates to Sailthru.',
          errormsg: 'You must select at least one template to import!',
          source: ''
        }
      });
      this.revealModal();
      return;
    }

    axios.post(`/templates`, {
      list: this.state.selected_templates,
      include_teams: this.state.include_teams,
      src: {
        apiKey: this.state.src_key,
        secret: this.state.src_secret
      },
      dest: {
        apiKey: this.state.dest_key,
        secret: this.state.dest_secret,
      }
    })
    .then(messages => {
     if (!Array.isArray(messages.data) && messages.data.hasOwnProperty('errormsg')) {
        this.setState({error: messages.data});
        this.revealModal('error');
      } else {
        this.setState({messages: messages.data});
        this.revealModal('console');
      }
    })
    .catch(err => console.log(err));
  }

  hideModal() {
    document.querySelector('#modal-container')
            .classList
            .add('invisible');

    document.querySelector(`#${this.state.modal_type}-modal-content`)
            .classList
            .add('invisible');
  }

  revealModal(type) {
    this.setState({modal_type: type});

    document.querySelector('#modal-container')
            .classList
            .remove('invisible');

    document.querySelector(`#${type}-modal-content`)
            .classList
            .remove('invisible');
  }

  render() {
    return(
      <div className="flex column space-around container-xxl full-page">
        <div className="flex space-between full-page container-xxl">
          <img src={Sailthru_Logo} id="logo" />
          <h3 id="title">TEMPLATE TRANSFER TOOL</h3>
        </div>
        <div className="flex row space-around container-xxl full-page">
          <div className="flex column center">
            <ImportContainer
              handleChange={this.handleChange.bind(this)}
              src_key={this.state.src_key}
              src_secret={this.state.src_secret}
              getList={this.getList.bind(this)}
            />
          </div>
          <div className="flex row space-around container-xxl">
            <TemplateListContainer
              selectTemplate={this.selectTemplate.bind(this)}
              selected_templates={this.state.selected_templates}
              templates={this.state.templates}
              importFromList={this.importFromList.bind(this)}
              selectAll={this.selectAll.bind(this)}
              deselectAll={this.deselectAll.bind(this)}
              toggleOption={this.toggleOption.bind(this)}
            />
          </div>
          <div className="flex column center">
            <ST_Dest
                handleChange={this.handleChange.bind(this)}
                dest_key={this.state.dest_key}
                dest_secret={this.state.dest_secret}
                importFromList={this.importFromList.bind(this)}
              />
          </div>
        </div>
          <ModalContainer
            messages={this.state.messages}
            hideModal={this.hideModal.bind(this)}
            error={this.state.error}
          />
          <p><i>Note: To prevent errors, this tool removes from_email, promotion_id, link domains, data feeds and teams from all transfers.</i><br/>
          <b>This transfer will overwrite includes of the same name in your target account.</b></p>
      </div>
    );
  }
}



