import React from 'react';

const Checkbox = (props) => {
  if (props.selected_templates.includes(props.template)) {
    return (
      <div>
        <input
          type="checkbox"
          className="menu-item-checkbox"
          name="selected_templates"
          value={props.template}
          onChange={props.selectTemplate}
          checked="true" />
      </div>
    )
  } else {
    return (
      <div>
        <input
          type="checkbox"
          className="menu-item-checkbox"
          name="selected_templates"
          value={props.template}
          onChange={props.selectTemplate}
        />
      </div>
    )
  }
};

export default Checkbox;



