import React from 'react';
import ST from './st';

const ImportMenu = (props) => {
  return(
    <div className="flex row space-around container-xxl">
      <ST
        handleChange={props.handleChange}
        getList={props.getList}
        src_key={props.src_key}
        src_secret={props.src_secret}
      />
    </div>
  )
}

export default ImportMenu;



